import React from 'react';
import { Link } from 'react-router-dom';

import './main-navigation.scss';

const MainNavigation = ({ onClickLink }) => {

    const handleLinkClick = () => {
        onClickLink && onClickLink();
    }

    return (
        <ul>
            <li>
                <Link to="/" onClick={handleLinkClick}>  </Link>
            </li>
            <li>
                <Link to="/about-us" onClick={handleLinkClick}>   </Link>
            </li>
            <li>
                <Link to="/contacts" onClick={handleLinkClick}>  </Link>
            </li>
            <li>
                <Link to="/shop" onClick={handleLinkClick}>  </Link>
            </li>
            <li>
                <Link to="/payment-delivery" onClick={handleLinkClick}>    </Link>
            </li>
        </ul>
    )
}

export default MainNavigation
